import { ReplaySubject } from "rxjs";
import { BubbleSort } from "./bubble-sort";
import { SelectionSort } from "./selection-sort";
const availableAlgorithms = [
    {
        name: "bubble sort",
        sort: (nums) => new BubbleSort(nums).sort(),
    },
    {
        name: "selection sort",
        sort: (nums) => new SelectionSort(nums).sort(),
    },
];
const withSortingEvents = (action) => {
    const stream = new ReplaySubject();
    action((indices) => stream.next({ indices, type: "inspected" }), (indices) => stream.next({ indices, type: "swapped" }));
    stream.complete();
    return stream;
};
const swap = (data, first, second) => {
    const temp = data[first];
    data[first] = data[second];
    data[second] = temp;
};
export { availableAlgorithms, withSortingEvents, swap, };
